.catalog-card {
  color: $color-alice-blue;
  line-height: 19.6px;
  width: 260px;
  height: 386px;

  display: grid;
  grid-template-rows: fit-content(0);

  @include vp-1159 {
    width: 309px;
  }

  @include vp-767 {
    width: 270px;
  }

  &:hover {
    background-color: $color-tangaroa;
  }

  &:hover .catalog-card__picture {
    display: none;
  }

  &:hover h3 {
    align-self: start;
    margin: 27px 30px 16px 25px;

    @include vp-1159 {
      margin-left: 34px;
    }

    @include vp-767 {
      margin-left: 25px;
    }
  }

  &:hover .catalog-card__side-back {
    display: grid;
    grid-template-rows: 1fr min-content;
    grid-column: 1/2;
    grid-row: 2/3;
    max-height: 100%;
  }
}

.catalog-card__picture {
  grid-column: 1/2;
  grid-row: 1/3;
  background-color: $color-jordy-blue;
}

.catalog-card h3 {
  grid-column: 1/2;
  grid-row: 1/3;
  align-self: end;
}

.catalog-card picture,
.catalog-card img {
  width: 260px;
  height: 386px;
  object-fit: cover;

  @include vp-1159 {
    width: 309px;
  }

  @include vp-767 {
    width: 270px;
  }
}

.catalog-card__side-back {
  display: none;
  min-height: 316px;
  padding: 69px 25px 35px;
  box-shadow: 0 4px 4px $color-black-transparent;

  @include vp-1159 {
    box-shadow: none;
    padding-left: 34px;
    padding-right: 35px;
  }

  @include vp-767 {
    padding-left: 25px;
    padding-right: 25px;
  }
}

.catalog-card__side-back dl,
.catalog-card__side-back dt,
.catalog-card__side-back dd {
  margin: 0;
}

.catalog-card__side-back dl {
  margin-bottom: 13px;
  overflow-y: auto;
}

.catalog-card__side-back dt {
  font-weight: 500;
}

.catalog-card__side-back dd {
  color: $color-quartz;
  padding-bottom: 13px;
}

.catalog-card__button {
  padding: 10px 10px 9px;
}
